class MapArgentina extends Component {
  static propTypes = {
    data: PropTypes.arrayOf(PropTypes.object),
  }

  state = {
    active: {
      name: 'Seleccione una provincia',
      value: -1
    },
  }


  handleMouseOver(name) {

    this.setState(() => ({
      active: {
        name: name,
        value: this.props.data.find(row => row.name === name) ? this.props.data.find(row => row.name === name).value : 0.00,
      },
    }));
  }


  getRegionFillColor(regionId) {
    const { data } = this.props;
    const ratio = (data.find(row => row.name === regionId) || { value: 0 }).value * 15 + 0.05;
    const opacity = ratio > 1 ? 1 : ratio;

    return `rgba(98, 50, 150, ${opacity})`;
  }

  getProvinceValue(value) {
    if (value >= 0) {
      return <p className='province-value'>{value !== 0 ? Math.round(value * 10000) / 100 : value} %</p>
    } else {
      return <p />
    }
  }

  render() {
    //const { data, isPercentual = false } = this.props;
    const value = this.state.active.value;
    return (
      <div className='map-argentina-container'>
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 500 800" width={300} height={500} className='continent'>
          <g>
            {Object.keys(PROVINCES).map(region => (
              <path key={region} id={region} title={region} d={PROVINCES[region]} fill={this.getRegionFillColor(region)}
                onMouseOver={() => this.handleMouseOver(region)} onClick={() => this.handleMouseOver(region)} />
            ))}
          </g>
        </svg>
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 2200 2200" width={100} height={100} className='caba'>
          <g>
            <path title={'CABA'} d={CABA} onMouseOver={() => this.handleMouseOver('Ciudad Autónoma de Buenos Aires')} onClick={() => this.handleMouseOver('Ciudad Autónoma de Buenos Aires')} fill={this.getRegionFillColor('Ciudad Autónoma de Buenos Aires')} />
          </g>
        </svg>
        <svg xmlns="http://www.w3.org/2000/svg" version="1.1" viewBox="0 0 600 500" width={40} height={20} className='malvinas'>
          <g>
            <path title={'MALVINAS'} d={MALVINAS} onMouseOver={() => this.handleMouseOver('Islas Malvinas')} onClick={() => this.handleMouseOver('Islas Malvinas')} fill={this.getRegionFillColor('Islas Malvinas')} />
          </g>
        </svg>
        <div className='description'>
          <p className='province-name'>{this.state.active.name}</p>
          {this.getProvinceValue(value)}
        </div>
      </div>
    );
  }
}

export default MapArgentina;